@use 'base/variables' as v;
@use 'base/mixins' as m;

table.propiedades{
    margin-top: 4rem;
    width: 100%;
    // border-spacing: 0;

    thead{
        background-color: v.$verde;

        th{
            color: v.$blanco;
            border-right: 5px solid v.$blanco;
            padding: 1rem;

            &:last-of-type{
                border-right: none;
            }
        }

        
    }

    tbody{

        tr,td{
            border-bottom: 1px solid v.$gris;

            td{
                text-align:center;
                font-weight: bold;
                
            }
        }
    }

    .imagen-tabla{
        width: 10rem;
        height: auto;
        margin: auto;

    }

    .boton:last-of-type{
        margin-top: 1rem;
    }
}

.imagen-actualizar{
    width:25rem;
    height:17rem;
    margin: 0 auto;
}