@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    padding: calc(v.$separacion / 2) 0;
    background-color: v.$grisOscuro;
    margin-bottom: 0;

    .contenido-footer{
        padding-top: calc(v.$separacion / 2);
    }

    .navegacion{
        visibility: visible;
        opacity: 1;
        display:flex;
        margin-bottom: 2rem;
        a{
            margin-right: 2rem;
        }
    }

    .copyright{
        color: v.$blanco;
        text-align: center;
        padding-top: 2rem;
    }
}