@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario{

    p{ 
        font-size: 1.4rem;
        color: v.$grisOscuro;
        margin: 0;
        margin-top: 2rem;

    }

    legend{
        font-size: 2rem;
        color: v.$grisOscuro;
    }

    label{
        font-weight: bold;
        text-transform: uppercase;
        display:block;
    }

    input:not([type="submit"]),textarea, select{ //Elegimos todos los input excepto los que sean de tipo submit
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$gris;
        border-radius: 1rem;
        margin-bottom: 2rem;

    }

    textarea{
        height: 20rem;
    }

    .forma-contacto{
        max-width:30rem;
        display: flex;
        justify-content: space-between;
        align-items:center;
    }

    input[type="radio"]{
        width:auto;
        margin: 0;
    }
    
}