@use 'base/variables' as v;
@use 'base/mixins' as m;

.dark-mode{
    background-color: darken(v.$grisOscuro, 10);

    p, blockquote {
        color: v.$blanco;
    }

    a, h1, h2, h3, h4{
        color: v.$gris;
    }
    ul img{
        filter: invert(90%);
    }

    .precio{
        color: v.$verde;
    }

    .anuncio{
        background-color: v.$grisOscuro;
        border: 1px solid v.$grisOscuro;    
    }

    .formulario{

        fieldset{
            border: 1px solid v.$grisOscuro;
        }
        legend, label, p{
            color: v.$gris;
        
        }

        input:not([type="submit"]),textarea, select{
            color: v.$gris;
            background-color: darken(v.$grisOscuro, 5);
            border: 1px solid v.$grisOscuro;
        }
    }
}