@use 'base/variables' as v;
@use 'base/mixins' as m;


.derecha{
    display:flex;
    flex-direction: column;
    align-items: center;

    @include m.mediaq(v.$tablet){
        align-items: flex-end;
        
    }

    .dark-mode-boton{
        width: 3rem;
        margin: 3rem 0 2rem 0;
        filter: invert(100%);

        &:hover{
            cursor: pointer;
        }

        @include m.mediaq(v.$tablet){
            margin-top: 0;
        }
    }
}
.barra {

    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3rem;

    @include m.mediaq(v.$tablet){
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end
        
    }
}
.navegacion {

    
    visibility:hidden;
    max-height:0rem;
    opacity: 0;
    height: 0;
    -webkit-transition: visibility .6s ease-in-out, opacity .6s linear, max-height 1s linear; 
    -moz-transition: visibility .6s ease-in-out, opacity .6s linear, max-height 1s linear; 
    -ms-transition:visibility .6s ease-in-out, opacity .6s linear, max-height 1s linear; 
    -o-transition: visibility .6s ease-in-out, opacity .6s linear, max-height 1s linear; 
    transition: visibility .6s ease-in-out, opacity .6s linear, max-height 1s linear; 
    

    @include m.mediaq(v.$tablet){
        display:flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
        height: auto;
    }

    a{
        color: v.$blanco;
        text-decoration: none;
        font-size: 1.8rem;
        display:block;
        text-align: center;

        @include m.mediaq(v.$tablet){
            margin-right: 2rem;

            &:last-of-type{
                margin-right: 0;
            }
            
        }
    }
}

// Menu de Hamburguesa

.mobile-menu{
    @include m.mediaq(v.$tablet){
        display: none;
    }

    img{
        width:5rem;
    }

}

.mostrar{

    visibility:visible;
    opacity: 1;
    height: auto;
    max-height: 50rem;
    

}
