@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-nosotros {

    @include m.mediaq(v.$tablet){
        @include m.grid(2, 2rem);
    }
}
.texto-nosotros {

    blockquote{
        margin:3rem 0 0 0;
        font-size: 2.2rem;
        font-weight: bold;
        padding: 1rem 0 3rem 0;
    }
}
