@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-webp .inicio{
    background-image: url(../img/header.jpg);
}

.webp .inicio{
    background-image: url(../img/header.webp);
}

.header {
    background-color: v.$grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio{
        @include m.bg-image; 
        
        @include m.mediaq(v.$tablet){
            min-height: 70rem;
        }
    }
}

.inicio .contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

 

    @include m.mediaq(v.$tablet){
        min-height: 70rem;

        h1{
            font-size: 3.8rem;
            text-align: left;
            line-height: 2;
            max-width: 60rem;
        }
    }    
}

.contenido-header h1{
    line-height:1.5;
    font-size:3rem;
    color: v.$blanco;
    font-weight: v.$bold;
     
}


