@use 'base/variables' as v;
@use 'base/mixins' as m;


.errores{
    display: block;

    .alerta{
        color:v.$blanco;
        font-weight: bold;
        text-align: center;
        padding:.5rem;
        font-size:1.6rem;
        border-radius: 5px;
        text-transform: uppercase;

        &.error{
            background-color: v.$rojo;        
        }
        
        &.exito{
            background-color: v.$verde;       
        }
    }
    



}
