@use 'base/variables' as v;
@use 'base/mixins' as m;

@include m.mediaq(v.$tablet){

    .contenedor-anuncios{
    
        @include m.grid(3, 2rem);

    }
}

.anuncio{
    background-color: lighten(v.$gris, 5);
    border-radius: 0.8rem;
    border:1px solid darken(v.$gris,8);
    margin-bottom: 2rem;

    &:last-of-type{
        margin-bottom: 0;
    }

    @include m.mediaq(v.$tablet){
        margin-bottom:0;
    }

    .contenido-anuncio{
        padding: 2rem;
    }

    h3,p{
        margin: 0;
    }


}

.precio{
    color: v.$verde;
    font-weight: v.$bold;
    font-size: 3rem;
}

.iconos-caracteristicas{
    display:flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;
    li{
        flex:1;
        display:flex;

        img{
            flex: 0 0 3rem;
            margin-right: 1rem;
        }

        p{
            font-weight: bold;
        }

    }
}