@use 'base/variables' as v;
@use 'base/mixins' as m;

@include m.mediaq(v.$tablet){
    .seccion-inferior{
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog{
    margin-bottom: 2rem;

    @include m.mediaq(v.$tablet) {
        display:grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }

    &:last-of-type{
        margin-bottom: 0;
    }
}

.informacion-meta{
    span{
        color: v.$amarillo;
        font-weight: bold;
    }
}

.texto-entrada{
    a{
        color: v.$negro;
        display: block;
        margin-top: 2rem;

        @include m.mediaq(v.$tablet){
            margin-top: 0;
        }
    }

    h4{
        margin: 0;
        line-height: 1.4;
        &::after{ //Creamos un separador con una linea  entre el titulo y la fecha de publicaci√≥n con una anchura y altura determinadas
            content:'';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: v.$verde;
            margin-top: 1rem;
        }
    }


}