@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonial{

    background-color: v.$verde;
    padding: 2rem;
    color: v.$blanco;
    font-size:2.4rem;
    line-height: 2.2;
    font-weight: 100;
    border-radius: 2rem;

    blockquote{

        position:relative;
        
        padding-left: 5rem;
        &::before{
            content:'';
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            left: -2rem;
            
        }
    }

    p{
        text-align:end;
        font-weight: bold;
        color:v.$blanco
    }
}